import { Component, EventEmitter, Injectable, ElementRef, NgZone, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, Validators } from '@angular/forms';
import { UploadOutput, UploadInput, UploadFile, humanizeBytes, UploaderOptions } from 'ngx-uploader';
import { HttpClient, HttpParams, HttpRequest, HttpEvent} from '@angular/common/http';
import { ValidationService } from '../../services/validation.service';
import { MapsAPILoader } from '@agm/core';
import { DriversService } from '../../services/drivers.service';
import { ToastrService } from 'ngx-toastr';
import { StateService } from '../../services/state.service';
import { CountryService } from '../../services/country.service';
import { CityService } from '../../services/city.service';

@Component({
  selector: 'app-driversedit',
  templateUrl: './driversedit.component.html',
  styleUrls: ['./driversedit.component.scss']
})
export class DriverseditComponent implements OnInit {

  userForm: any;
  driverid;
  formData: FormData;
  files: UploadFile[];
  uploadInput: EventEmitter<UploadInput>;
  humanizeBytes: Function;
  dragOver: boolean;
  msg: any = null;
  options: UploaderOptions;
  Drivers:any;
  lastChar:any;  

  public lat: any = '';
  public lng: any = '';
  public address: any = '';

  Countries: any[] = [{'countryName':'--Select--', '_id':''}];
  States: any[] = [{'stateName':'--Select--', '_id':''}];
  Cities: any[] = [{'cityName':'--Select--', '_id':''}];
  gender: any[] = [{'name':'--Select--', 'value':''}, {'name':'Male','value':'Male'}, 
  {'name':'Female','value':'Female'}];
  default: string = '';


  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private service: DriversService,
    private countryservice: CountryService,
    private stateservice: StateService,
    private cityservice: CityService,
    private toastr: ToastrService,
    private _router: Router,
    public http: HttpClient,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
      
  ) {
      
   

    this.route.params.subscribe((params) => {
      this.driverid = params['driverid'];
      console.log(this.driverid)

      this.options = { concurrency: 1, maxUploads: 3 };
      this.files = []; // local uploading files array
      this.uploadInput = new EventEmitter<UploadInput>(); // input events, we use this to emit data to ngx-uploader
      this.humanizeBytes = humanizeBytes;
    });

    this.userForm = this.formBuilder.group({
      "_id": [this.driverid, Validators.required],
      "userFirstName": ['', Validators.required],
      "userLastName": ['', Validators.required],
      "userEmail": ['', [Validators.required, ValidationService.emailValidator]],
      "userPhone": ['', Validators.required],
      "userRole": "Driver",
      "userPassword": ['', Validators.required],
      "country": ['', Validators.required],
      "state": ['',],
      "city":  ['',],
      "gender" : ['', Validators.required],
      "userAddress": ['', Validators.required],
      "driverLicence": ['',],
      "userClassLicence":['', Validators.required],
      "selfieImage": ['', Validators.required],
      "documentImage": ['', Validators.required]
    });
    this.userForm.controls['country'].setValue(this.default, {onlySelf: true});
    this.userForm.controls['gender'].setValue(this.default, {onlySelf: true});
    this.userForm.controls['state'].setValue(this.default, {onlySelf: true});
  
  }

  
  
  saveUser() {
    if(this.userForm.value.userFirstName == ""){
      this.toastr.error('Usr name is required');
      return false;
    }
    console.log(this.userForm.value)
    if (this.userForm.dirty && this.userForm.valid) {

    
        this.service.addDriver(this.userForm.value).subscribe(
            data => {
                console.log(data)
                this.toastr.success('Driver added Successfully');
                this._router.navigate(['/driver']);
            },
            error => {
               console.log('error')
            });
       
    }
  }
    
  autoCompleteCallback1(data: any): any {
   // alert(data)
    if(data.data.formatted_address == ''){
      this.address ="";
    }else{
    this.address = JSON.stringify(data.data.formatted_address);
    }
    this.userForm.controls['userAddress'].setValue(this.address);
    
  }
  ngOnInit() {
      this.getDrivers();
      this.getCountries();
     }
  
  myUploaderNew(event1) {
      this.service.uploadLogo(event1).subscribe(
        data => {
          console.log(data);
        this.userForm.controls['selfieImage'].setValue(data['path'].selfieImage);
        this.userForm.controls['documentImage'].setValue(data['path'].selfieImage);
        //  this.profile_image = data.data.url;
         this.msg = 'Uploded successfully';
        },
        error => {
           console.log('error')
        });
     }

     getDrivers() {
       let query = '/'+this.driverid;
        this.service.getdDrivers(query).subscribe(res => {
        
        this.Drivers = res['driver'].user;
        this.getStates(this.Drivers.country._id);
        this.getCities(this.Drivers.state._id);
      
      // console.log(res)
       console.log(this.Drivers)
          this.userForm.controls['_id'].setValue( this.Drivers.id, {onlySelf: true});
          this.userForm.controls['userFirstName'].setValue( this.Drivers.userFirstName, {onlySelf: true});
          this.userForm.controls['userLastName'].setValue( this.Drivers.userLastName, {onlySelf: true});
          this.userForm.controls['userEmail'].setValue( this.Drivers.userEmail, {onlySelf: true});
          this.userForm.controls['userPhone'].setValue( this.Drivers.userPhone, {onlySelf: true});
          this.userForm.controls['userRole'].setValue( this.Drivers.userRole, {onlySelf: true});
          this.userForm.controls['country'].setValue( this.Drivers.country._id, {onlySelf: true});
          this.userForm.controls['state'].setValue( this.Drivers.state._id, {onlySelf: true});
          this.userForm.controls['city'].setValue( this.Drivers.city._id, {onlySelf: true});
          this.userForm.controls['gender'].setValue( this.Drivers.gender, {onlySelf: true});
          this.userForm.controls['userAddress'].setValue( this.Drivers.userAddress, {onlySelf: true});
          this.userForm.controls['driverLicence'].setValue( res['driver'].driverLicence, {onlySelf: true});
          this.userForm.controls['userClassLicence'].setValue( this.Drivers.userClassLicence.className, {onlySelf: true});
          this.userForm.controls['selfieImage'].setValue( this.Drivers.selfieImage, {onlySelf: true});
          this.userForm.controls['documentImage'].setValue( this.Drivers.documentImage, {onlySelf: true});
      
     });
    }

    updateDriver(){
      let query = '/'+this.driverid;
      this.service.updateDriver(this.userForm.value, query).subscribe(
        data => {
            console.log(data)
            this.toastr.success('Driver updated Successfully');
            this._router.navigate(['/driver']);
        },
        error => {
           console.log('error')
        });
    }

    getCountries() {
      this.countryservice.getCountries().subscribe(res => {
        this.Countries = res['countries'];
     });
    }

    getStates(id) {
      let cid;
      if(this.userForm.value.country==''){
         cid = id;
      }else{
         cid = this.userForm.value.country;
      }
       let query = '?country_id='+cid;
     // let query = '?country_id='+'5b64792806ff811e1f06f0d0';
      this.stateservice.getStates(query).subscribe(res => {
        console.log(res)
        this.States = res['states'];
     });
    }
   
    getCities(id) {
      let cid;
      if(this.userForm.value.state==''){
         cid = id;
      }else{
         cid = this.userForm.value.state;
      }
      let query = '?state_id='+cid;
      this.cityservice.getCities(query).subscribe(res => {
        this.Cities = res['cities'];
     });
    }

}
